.sliders {
  overflow: hidden;
  position: relative;
  width: 100vw;
  &__carousel {
    margin: 0;
    padding: 0; }
  img {
    width: 100%; }
  &__prev, &__next {
    position: absolute;
    z-index: 1;
    top: calc(50% - 40px);
    font-size: 80px;
    color: #ffffff;
    transition: .5s;
    &:hover {
      color: #ffc06f; } }
  &__prev {
    left: -50px; }
  &__next {
    right: -50px; }
  &:hover &__prev {
    left: 30px; }
  &:hover &__next {
    right: 30px; } }
