.hamburger-button {
  display: none; }

.header {
  z-index: 999;
  position: fixed;
  width: 100%;
  top: 0;
  right: 0;
  background: #666666;
  transition: .5s;
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100px;
    transition: .5s; }
  &.scroll {
    padding: 0;
    .header {
      &__container {
        width: 100%;
        top: 0;
        left: 0;
        align-items: center;
        height: 70px; } }
    .logo {
      &__header {
        font-size: 30px;
        line-height: 30px; }
      &__footer-decor {
        width: 8px;
        height: 8px;
        background: #f6f5c5; }
      &__footer {
        align-items: center; }
      &__footer-text {
        font-size: 16px;
        color: #f6f5c5; } } } }


.logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  &__header {
    font-size: 50px;
    line-height: 50px;
    color: #ffc06f;
    transition: .5s; }
  &__footer {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    width: 100%; }
  &__footer-decor {
    width: 16px;
    height: 16px;
    background: #f6f5c5; }
  &__footer-text {
    font-size: 24px;
    color: #f6f5c5; } }

.nav {
  display: flex;
  align-items: center;
  list-style-type: none;
  padding: 0;
  margin: 0;
  &.active {
    display: flex; }
  &__part {
    font-size: 18px;
    line-height: 45px;
    padding: 0 20px;
    font-weight: 500;
    letter-spacing: 6px;
    text-decoration: none;
    color: #000;
    border-top: 3px solid #cecece;
    border-bottom: 3px solid #cecece;
    background: #fff;
    &:hover, &.active {
      color: #ffc06f;
      border-top: 3px solid #ffc06f;
      border-bottom: 3px solid #ffc06f; } } }
