.up {
  position: fixed;
  bottom: -80px;
  right: 30px;
  z-index: 999;
  transition: bottom 1s;
  &__icon {
    font-size: 20px;
    line-height: 30px;
    text-align: center;
    color: #f6f5c5;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: rgba(black, .2);
    border: 2px solid #f6f5c5; }
  &.active {
    bottom: 30px; } }
