.gallery {
  margin-bottom: 60px;
  &__albums {
    display: flex;
    align-items: center;
    justify-content: space-between; }
  &__album {
    position: relative;
    width: 320px;
    height: 250px;
    overflow: hidden; }
  &__album:before {
    color: #ffc06f;
    opacity: 0;
    z-index: 1000;
    font: normal normal normal 14px/1 FontAwesome;
    content: '\f00e';
    width: 50px;
    height: 50px;
    position: absolute;
    left: calc(50% - 25px);
    top: calc(50% - 25px);
    font-size: 50px; }
  &__album-img {
    transition: transform 2s; }
  &__album:hover {
    .gallery__album-img {
      transform: scale(1.4); }
    &:before {
      opacity: 1; } } }
