.preloader {
  z-index: 100000;
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: black; }

@keyframes loader {
  100% {
    transform: rotate(360deg); } }
.preloader__spiner {
  font-size: 50px;
  position: absolute;
  top: calc(50% - 25px);
  left: calc(50% - 25px);
  text-align: center;
  width: 50px;
  height: 50px;
  line-height: 50px;
  color: white;
  animation: loader 1s infinite linear; }
