.title {
  border-top: 2px solid #f6f5c5;
  border-bottom:  2px solid #f6f5c5;
  display: flex;
  align-items: center;
  margin: 33px 0;
  width: 100%;
  &__decor {
    width: 24px;
    height: 24px;
    background: #f6f5c5; }
  &__text {
    font-size: 31px;
    font-weight: 500;
    letter-spacing: 8px;
    line-height: 90px;
    margin: 0 15px; } }
.biography {
  &__main {
    background: #666666; }
  &__container {
    display: flex; } }
.biography-text {
  margin: 0 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  &__title {
    text-align: center;
    margin-top: 30px;
    margin-bottom: 30px; }
  &__title-header {
    font-size: 61px;
    color: #ffc06f; }
  &__title-footer {
    font-size: 31px;
    color: #f6f5c5; }
  &__main {
    font-size: 16px;
    color: #fff;
    margin-bottom: 50px; }
  &__cap-letter {
    font-size: 31px;
    color: #f6f5c5;
    &-small {
      font-size: 16px; } }
  &__more {
    color: #000;
    text-align: center;
    width: 95px;
    height: 30px;
    line-height: 30px;
    background: white;
    border: 1px solid #ffc06f;
    position: absolute;
    right: 0;
    bottom: 10px;
    text-decoration: none; } }
